.assetmanager-modal-container {
  .modal-content {
    .heading {
      p {
        span {
          color: #0f5efd;
        }
      }
    }

    .modal-body {
      padding: 0px 10px;
      color: #3b4859;

      .modal-submit-button {
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
      }
    }
  }
}


.select-account-modal-container {
  max-width: 550px !important;
  -webkit-box-shadow: 0px 0px 16px #3b48593d;
  box-shadow: 0px 0px 16px #3b48593d;
  border-radius: 10px;
  background: white;
  margin: 0 auto;

  .modal-content {
    padding: 15px;

    .modal-header {
      background: white;
      box-shadow: none;
      border-bottom: none;
      margin-bottom: 20px;
      padding: 0 10px 10px;
      position: relative;

      .modal-title {
        color: #383874 !important;
        text-align: center !important;
        margin: 0px;
        font-size: 14px;
        font-weight: 600;
        padding: 0;
        line-height: 20px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        width: 100%;
      }

      button {
        position: absolute;
        right: 0;
        top: 0px;
        cursor: pointer;
        color: #b5b5cf;
        opacity: 1;
        border: none;
        background-color: transparent;

        &:hover {
          color: #3b4859;
        }

      }
    }

    .modal-body {
      padding: 0px 10px;
      color: #3b4859;

      &::-webkit-scrollbar {
        width: 16px;
      }

      &::-webkit-scrollbar-track {
        background-color: transparent;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #d6dee1;
        border-radius: 10px;
        border: 6px solid transparent;
        background-clip: content-box;

        &:hover {
          background-color: #a8bbbf;
        }
      }

      h4 {
        font-size: 14px;
        line-height: 18px;
        font-weight: 600;
        color: #383874;
      }

      p {
        font-size: 12px;
        line-height: 18px;
        font-weight: 300;
        color: #838b9c;
      }

      input {
        margin: 0;
      }

      label {
        font-size: 12px;
        line-height: 12px;
        font-weight: 400;
        color: #383874;
        padding-left: 8px;
        padding-top: 0px;
        margin-top: 0px;
        margin-bottom: 0px;
        cursor: pointer;
      }

      .form-group {
        display: block;
        width: 100%;
        position: relative;
        margin-bottom: 15px;

        label {
          text-align: left;
          display: block;
          font-size: 16px;
          line-height: 20px;
          font-weight: 500;
          color: #000000;
          margin-bottom: 5px;
          padding-left: 0;
        }

        .form-control {
          text-align: left;
          display: block;
          width: 100%;
          height: 32px;
          padding: 2px 10px;
          border: 1px solid #dcdfe5;
          border-radius: 5px;
          font-size: 13px;
          line-height: 26px;
          font-weight: 400;
          color: #344054;
          font-family: "Poppins", sans-serif;

          &::placeholder {
            color: #b5b5cf;
          }
        }

        textarea {
          &.form-control {
            height: 80px;
            padding: 10px;
            color: #344054;
            line-height: 16px;
          }
        }

        .red {
          font-size: 12px;
        }
      }

      .environment-box {
        background: #ffffff;
        border-radius: 8px;
        border: 1px solid #b5b5cf;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100px;
        cursor: pointer;

        .environment-title {
          font-size: 12px;
          line-height: 16px;
          font-weight: 500;
          color: #9a9aaf;
        }

        .envir-image {
          background-repeat: no-repeat;
          display: block;
          width: 25px;
          height: 25px;
          margin: 0 auto 10px;
        }

        .department {
          background-image: url("../../img/assetmanager/envir-light1.png");
        }

        .testing {
          background-image: url("../../img/assetmanager/envir-light2.png");
        }

        .stage {
          background-image: url("../../img/assetmanager/envir-light3.png");
        }

        .production {
          background-image: url("../../img/assetmanager/envir-light4.png");
        }

        &.active {
          border: 1px solid #383874;

          .environment-title {
            color: #383874;
            font-weight: 600;
          }

          .department {
            background-image: url("../../img/assetmanager/envir-dark1.png");
          }

          .testing {
            background-image: url("../../img/assetmanager/envir-dark2.png");
          }

          .stage {
            background-image: url("../../img/assetmanager/envir-dark3.png");
          }

          .production {
            background-image: url("../../img/assetmanager/envir-dark4.png");
          }
        }
      }

    }

    .footer-top-bar {
      button.gray-outline {
        padding: 20px 15px 0;
        background: none;
        border: none;
        margin-right: 15px;
      }

      button.primary-btn {
        min-width: 100px;
      }

      button.primary-outline-btn {
        margin-right: 15px;
        min-width: 100px;
      }
    }
  }
}